import {
  type JsonSearchParams,
  type RecursiveSearchParams
} from '../../types/utils'

export const search = (query: JsonSearchParams, jsonObj: any): Array<any> => {
  // If nothing was requested, return the current object
  if (!query.keyToMatch) return [jsonObj]

  // Tokenize the search terms
  const tokenizeKeys = query.keyToMatch.split('.')

  // Add either wild card at the begining unless told not too or it's already there
  if (tokenizeKeys[0] === '$') tokenizeKeys.shift()

  // Call the recursive search
  const result = searchRec({
    valueToMatch: query.valueToMatch,
    tokenizeKeys,
    parent: jsonObj,
    limit: query.limit || Infinity,
    results: []
  }, jsonObj)
  return result
}

const searchRec = (query: RecursiveSearchParams, jsonObj: any): Array<any> => {
  const { valueToMatch, parent, results, limit } = query
  // If jsonObj is undefined or we have enough results we can return
  if (typeof jsonObj === 'undefined' || results.length >= limit) return results

  // Move the parent pointer
  query.parent = jsonObj

  // Going over each Array child with the same query params
  if (Array.isArray(jsonObj)) {
    for (const child of jsonObj) {
      searchRec({ ...query }, child)
    }
  } else if (
    !query.tokenizeKeys.length
  ) {
    // If we ran out of tokenizeKeys it means we need to verify
    // that we found the right key with the correct value and if
    // So add the parent to the results list
    console.log('valueToMatch', valueToMatch)
    console.log('jsonObj', jsonObj)
    console.log('parent', parent)
    console.log('query.tokenizeKeys', query.tokenizeKeys)
    if (valueToMatch === undefined) {
      results.push(jsonObj)
    }
    if (valueToMatch === jsonObj) {
      results.push(parent)
    }
  } else if (typeof jsonObj === 'object') {
    // Get a mutable version of tokenizeKeys
    let { tokenizeKeys } = query

    // If it's a recursive token, decent into all children
    // And remove the recursive token from the tokenizeKeys array
    if (tokenizeKeys[0] === '') {
      for (const key in jsonObj) {
        searchRec({ ...query }, jsonObj[key])
      }
      tokenizeKeys = tokenizeKeys.slice(1)
    }

    // Decent into child based on the first key in tokenizeKeys
    // With jsonObj as the parent and shifted tokenizeKeys
    const childKey = tokenizeKeys[0]
    tokenizeKeys = tokenizeKeys.slice(1)
    searchRec({ ...query, tokenizeKeys }, jsonObj[childKey])
  }

  return results
}
